*{
    margin: 0px;
    padding: 0px;
}
.Card{
    width: 23%;
    -webkit-box-shadow: 0px 0px 5px black;
            box-shadow: 0px 0px 5px black;
    border-radius: 5px;
    padding: 10px;
    font-family: sans-serif;
    margin-bottom: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: #fff;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
    position: relative;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.Card:hover{
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
    -webkit-box-shadow: 0px 0px 30px #fff;
            box-shadow: 0px 0px 30px #fff;
}
.Card__img{
    height: 160px;
    margin-bottom: 5px;
    -o-object-fit: cover;
       object-fit: cover;
}
.filmName{
    font-size: 25px;
    font-weight: bold;
    word-wrap: break-word;
    margin-bottom: 5px;
}
.btn{
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    -webkit-box-shadow: 0px 5px 10px gray;
            box-shadow: 0px 5px 10px gray;
    border:  none;
    font-size: 25px;
    background: #e2b100;
    color: white;
    -ms-flex-item-align: end;
        align-self: flex-end;
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
    cursor: pointer;
}
.btn:hover{
    -webkit-box-shadow: 0px 8px 10px gray;
            box-shadow: 0px 8px 10px gray;
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
    background: #2196f3;
}
.Card__descriptio{
    height: 80px;
    font-family: cursive;
    font-size: 14px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    margin-bottom: 15px;
    word-wrap: break-word;
}
.Card__rating{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 70px;
    height: 50px;
    border-radius: 5px 0px 50% 0px;
    background: #e2b100;
    font-family: sans-serif;
    font-size: 25px;
    font-weight: 700;
    position: absolute;
    left: 0px;
    top: 0px;
}
.Card__release{
    font-size: 20px;
    font-family: fantasy;
}
.filters__item{
    display: inline-block;
    margin-right: 10px;
    background: -webkit-gradient(linear, left bottom, left top, from(#2196f3), to(#ff8f00));
    background: -webkit-linear-gradient(bottom, #2196f3, #ff8f00);
    background: -o-linear-gradient(bottom, #2196f3, #ff8f00);
    background: linear-gradient(to top, #2196f3, #ff8f00);
    padding: 5px;
    border-radius: 5px;
}
.filters__item:last-child{
    margin-right: 0px;
}



