.HomePage{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 50px;
}
.fa-film{
    font-size: 200px;
    color: #cfd8dc;
}
.fa-film:hover{
    -webkit-transition: color 0.2s linear;
    -o-transition: color 0.2s linear;
    transition: color 0.2s linear;
    color: #ff8f00;
}
.HomePage__title{
    font-size: 40px;
    color: white;
    font-family: fantasy;
}
.HomePage__text{
    width: 800px;
    margin-top: 20px;
    text-align: center;
    font-size: 25px;
    font-family: sans-serif;
    color: white;
}
.text__link{
    color: white;
    cursor: pointer;
}
.text__link:hover{
    -webkit-transition: color 0.2s linear;
    -o-transition: color 0.2s linear;
    transition: color 0.2s linear;
    color: #ff8f00;
}



