.Stack{
    width: 1100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding-top: 100px;
}
.Stack__item{
    background: white;
    border-radius: 10px;
    padding: 50px 20px;
    -webkit-box-shadow: inset 0px 0px 30px 5px #2196f3, 0px 0px 30px 5px #2196f3;
            box-shadow: inset 0px 0px 30px 5px #2196f3, 0px 0px 30px 5px #2196f3;
}
.Stack__item:hover{
    -webkit-box-shadow: inset 0px 0px 30px 5px #e2b100, 0px 0px 30px 5px #e2b100;
            box-shadow: inset 0px 0px 30px 5px #e2b100, 0px 0px 30px 5px #e2b100;
}



