.MiniCard{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background: #0c161e;
    color: #fff;
    width: 100%;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    min-height: 140px;
}
.MiniCard:hover{
    -webkit-box-shadow: 5px 5px 12px 0 #534d4d;
            box-shadow: 5px 5px 12px 0 #534d4d;
}
.MiniCard__img{
    width: 120px;
    height: 120px;
    -o-object-fit: cover;
       object-fit: cover;
}
.MiniCard__content{
    width: 110px;
}
.MiniCard__filmName,
.MiniCard__releas{
    font-size: 16px;
    margin-bottom: 10px;
}
.MiniCard__rating{
    font-size: 16px;
}
.btn__delete,
.btn__delete:hover{
    -webkit-box-shadow: 0px 0px 0px 0px;
            box-shadow: 0px 0px 0px 0px;
}



